<template>

	<view class="container recharge ">
		<view class="bg-mine">
			<!-- 头像 -->
			<view class="bg-mine-portrait">
					<image :src="getUrl(img)" />
			</view>
			<!-- name -->
		<!-- 	<view class="  bal-money">
				<text>
					¥{{price}}
				</text>
			</view> -->
			<view class="bg-mine-name ">
				<text>红包余额:	¥{{price}}</text>
			</view>


		</view>
		<view class="history-title">
			<!-- <view> 充值历史</view> -->
		</view>

		<!-- 功能区域 -->
		<view id="recharge">
			<view class="recharge-container weui-line">
				<view class="weui-cell weui-cell_select weui-cell_select-after  ">
					<view class="weui-cell__hd">
						绑定手机：
					</view>
					
				
					
					<!-- #ifdef MP-WEIXIN -->
					<view class="weui-cell__bd wx-button "  v-if="wx">
						<button size="mini" type="theme" open-type="getPhoneNumber" @getphonenumber="decryptPhoneNumber" @click="getPhone">
							点击获取手机号
						</button>
					
					</view>
					<view v-if="normal" class="weui-cell__bd icon-select" id="" @click="changePhone">
						{{value}}
					</view>
					
					<!-- #endif -->
					<!-- <button>12312343434</button> -->
				
					
					
				</view>
				<!-- 		<view class="weui-cell weui-cell_select weui-cell_select-after weui-line">
					<view class="weui-cell__hd">
						<view  class="weui-view">缴费单位：</view>
					</view>
					<view class="weui-cell__bd icon-select" id="">
						123
					</view>
				</view> -->






			</view>
		</view>


		<!-- E-功能区域 -->



		<view class="clear" style="margin-top:   50px"> </view>


		<!-- 弹框区域 -->

		<uni-popup ref="popup" type="redpacket">

			<uni-popup-dialog :value="value" placeholder="请输入需要绑定的手机号码" mode="input" title="绑定手机号码" @close="close" @confirm="confirm"></uni-popup-dialog>

		</uni-popup>

		<!-- e-弹框区域 -->

	</view>



</template>
<script>
	import UniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'

	export default {
		name: 'rehistory',

		data() {
			return {
                img:'/static/images/photo.png',
				value: 13908368139,
				price:1859798779.95,
				wx:true,
				normal:false
			}
		},


		components: {

			UniPopup,
			uniPopupDialog


		},
		onLoad:function(){

		// 千位间隔分号
		
	    this.price=this.price.toString().replace(/(\d)(?=(\d{3})+\.)/g, function($0, $1) {return $1 + ","});
	 	this.img = uni.getStorageSync('userlist').user_img; 
	
			
		},
		

		methods: {
			
			getUrl(url){
				return  url;
			
			},
			
			decryptPhoneNumber:function(){
				// 获取手机号码
			},
			// 模拟通过接口获得手机号码后的样子
		getPhone:function(){
			var _this=this;
			
			setTimeout(function(){
				_this.wx=false;
				_this.normal=true;
			},2000)
			
		},
	
			// 关闭
			close(done) {
				done()
			},
			// 修改
			confirm(done, val) {
				// 需要验证不？需要！
				var value = String(val).replace(/^\.+|[^\d.]/g, '');

				//判断号码是否为菲律宾号码段，符合10位

				var reg = /^(8|08|9|09)[0-9]{9}$/;


				// var value = value.replace(/^\.+|[^\d.]/g, '');

				// console.log(value)
				if (value.match(reg)) {
					this.value = value;
					// #ifdef MP-WEIXIN
					wx.showToast({
						title: '修改成功',
						icon: 'success',
						duration: 2000,

					})

					// #endif
					// 缺少支付宝,修改成功交互
					// #ifdef MP-ALIPAY

					my.showToast({
						type: 'success',
						content: '修改成功',
						duration: 2000,
						success: () => {
							// 调用成功之后
						},
						fail: () => {
							//调用失败之后
						},
						complete: () => {
							//调用之后都会执行
						}
					})



					// #endif

				} else {
					// #ifdef H5
					alert("请输入正确11位号码")

					// #endif
					// #ifdef MP-WEIXIN
					wx.showToast({
						title: '！请输入正确号码',
						icon: 'none',
						duration: 2000,

					})
					// #endif
					// #ifdef MP-ALIPAY
					my.showToast({

						content: '！请输入正确号码',

						duration: 2000

					});
					// #endif
				}

				// console.log(val);
				// 关闭
				done()
			},
			// 修改
			changePhone() {

				this.$refs.popup.open()

			}
		}


	}
</script>
<style>
	/* #ifdef H5 */
	@media screen and (min-width:750px) {
		.weui-cell{
			line-height: 1.5rem!important;
			height: 1.5rem!important;
		}
		.bg-mine{
			height: 360px!important;
		}
		
	}
	/* #endif */
	
	/* #ifdef MP-WEIXIN */
	.button-hover[type=theme] {
		color: hsla(0, 0%, 100%, .6);
		background-color: #942228
	}
	
	uni-button[type=theme] {
		color: #fff;
		background-color: var(--theme-color);
	}
	/* #endif */
	
	page {
		background-color: #f5f5f5;
	}

	body {
		margin: 0;

	}

	.bg-mine {
		width: 100%;
		height: 180px;
		background: green;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-image: linear-gradient(to bottom right, #e4393c, #ecd533)
	}

	.bg-mine .bg-mine-portrait {
		width: 80px;
		height: 80px;
		overflow: hidden;
		margin: 0;
		padding: 0;
		border-radius: 50%
	}

	.bg-mine .bg-mine-portrait image {
		width: 80px;
		height: 80px;
		border-radius: 50%;
		object-fit: cover
	}

	.bg-mine .bg-mine-name {
		font-size: 16px;
		color: #FFF;
		line-height: 2.5em;
		height: 2.5em;
		font-weight: bold;
	}

	.bg-mine .bal-money {
		font-size: 20px;
		color: #fff;
		line-height: 1.5em;
		height: 1.5em;
		font-weight: bold;
	}


	.weui-grid {
		position: relative;
		float: left;
		padding: 10px 5px;
		width: 50%;
		box-sizing: border-box;
		text-decoration: none;
	}



	.history-title {
		padding: 10px;
		font-size: 14px;
		font-weight: 700;
		color: #333;
		background-color: #f5f5f5;

	}

	.navigator-hover {
		opacity: .8;
		background-color: #ddd;
	}

	#recharge {

		font-size: 14px;
		color: #333;
	}

	.recharge-container {
		background-color: #FFF;
		position: relative;
		/* border-radius: 15px; */
	}

	.weui-cell {

		margin-left: 30rpx;
		/* margin-right: 30rpx; */
		background-color: #fff;
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		-webkit-box-align: center;
		-webkit-align-items: center;
		align-items: center;
		font-weight: 700;
		font-size: 1em;
		line-height: 2.5rem;
		height: 2.5rem;
		position: relative;
		/* #ifdef MP-ALIPAY */
		line-height: 2.6em;
		height: 2.6em;
		/* #endif */
	}

	.weui-cell__hd {
		width: 30%;
		text-align: left;
		font-weight: bold;
	}

	.weui-cell__bd {
		width: 70%;
		text-align: left;
		font-weight: 400;
		/* #ifdef MP-ALIPAY */
		line-height: 2.6em;
		height: 2.6em;
		/* #endif */
	}

	.mar-r15 {
		margin-right: 15px;
	}




	.icon-select:after {
		content: " ";
		display: inline-block;
		height: 10rpx;
		width: 10rpx;
		border-width: 2px 2px 0 0;
		border-color: #C8C8CD;
		border-style: solid;
		-webkit-transform: matrix(0.71, .71, -.71, .71, 0, 0);
		transform: matrix(0.71, .71, -.71, .71, 0, 0);
		position: relative;
		top: -2px;
		position: absolute;
		top: 50%;
		right: 20px;
		margin-top: -4px;
	}

	.weui-cell.weui-line:before {
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		height: 1px;
		border-top: 1px solid #e5e5e5;
		color: #e5e5e5;
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		left: 15px;
		z-index: 2;

	}
	.wx-button{
		display: flex;
		align-items: center;
	}
</style>
